<template>
    <div>
        <!-- 图形验证码、手机号、手机验证码、密码和确认密码 forget-->
        <div class="tit">
            找回密码
        </div>
        <div class="ruleFormBox">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="图形验证码" prop="text">
                <el-input class="L_inp" v-model="ruleForm.text" placeholder="请输入图片验证码">
                    <img :src="image_code_url" slot="suffix" class="Verification" @click="Fnimage_code_url()" alt="">
                </el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="phone_number">
                <el-input class="L_inp" v-model="ruleForm.phone_number" placeholder="请输入手机号">
                </el-input>
            </el-form-item>
            <el-form-item label="发送验证码" prop="phone_code">
                <el-input class="L_inp" v-model="ruleForm.phone_code" placeholder="请输入手机号验证码">
                    <el-button slot="suffix" class="Verification" :disabled="DYdisabled" @click="FnTimeY()" size="medium">{{wait}}</el-button>
                </el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="pass_word">
                <el-input class="L_inp" v-model="ruleForm.pass_word" type="password"  placeholder="请输入密码">
                </el-input>
            </el-form-item>
            <el-form-item label="确认新密码" prop="pass_word2">
                <el-input class="L_inp" v-model="ruleForm.pass_word2" type="password" placeholder="请输入密码确认">
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">找回密码</el-button>
            </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
    data() {
        var validatePass = (rule, value, callback) => {
            var reg = new RegExp(/^(\w){6,20}$/);
            if (value === '') {
            callback(new Error('请输入密码'));
            } else if(!reg.test(value)){
                callback(new Error('包含数字和字母，且长度为8-16位')); 
            }
            callback();
        }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass_word) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
        return {
            ruleForm:{//提交表单
                text: '',//图片验证码
                phone_number: '',//手机号
                phone_code: '',//手机号验证码
                pass_word: '',//新密码
                pass_word2: "",//确认新密码
            },
            rules: {//提交表单数值验证
                text: [
                    { required: true, message: '请输入图片验证码', trigger: 'blur' }
                ],
                phone_number: [
                    { required: true, pattern:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,message: '请输入正确格式的手机号', trigger: 'blur' }
                ],
                phone_code: [
                    { required: true, message: '请输入手机号验证码', trigger: 'blur' }
                ],
                pass_word: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                pass_word2: [
                    { validator: validatePass2, trigger: 'blur' }
                ]
            },
            image_code_url:'',
            DYdisabled:false,//验证码按钮是否被禁用
            wait:'发送验证码',//验证码的html文字
        };
    },
    methods: {
      FnTimeY(){//点击发送验证码触发的方法
        // 验证和验证码请求
        let reg=/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/
        if(reg.test(this.ruleForm.phone_number) && this.ruleForm.text !=''){
          if(!this.DYdisabled){ //这里要进行手机号格式验证
            this.DYdisabled=true
            let tiem=60
              url.yzm({
                image_code:this.ruleForm.text,
                uuid:this.uuid
              },this.ruleForm.phone_number).then(res=>{
                console.log(res,'------成功发送验证码')
                if(res.data.code==200){
                  this.$message.success('发送成功');
                  this.getCode=res.data.getCode
                }else{
                
                  this.$message.error(res.data.errmsg);
                }
              }).catch(err=>{
                console.log(err,'-------失败发送验证码')
              })
              let trmer=setInterval(() => {
              if(tiem<=0){
                clearInterval(trmer)
                this.wait='发送验证码'
                this.DYdisabled=false
              }else{
                this.wait=--tiem
              }
            }, 1000);
          }
        }else{
          this.$message.error('手机格式不正确或图片验证码为空');
        }
      },
      submitForm(formName) {//忘记密码表单提交
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // 接口请求数据
            url.forgetpassword(this.ruleForm)
            .then(res=>{
                if(res.data.code[0]==200){
                  this.$message.error(res.data.errmsg[0]);
                  this.$router.push('/login')
                }else{
                    this.$message.error(res.data.errmsg[0]);
                }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      Fnimage_code_url(){//获取验证码图片
        this.uuid=this.generateUUID();
        this.image_code_url=this.code_image_url+this.uuid+'/';
      },
      // 生成uuid
      generateUUID() {
          let d = new Date().getTime();
          if(window.performance && typeof window.performance.now === "function"){
              d += performance.now(); //use high-precision timer if available
          }
          let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
              let r = (d + Math.random()*16)%16 | 0;
              d = Math.floor(d/16);
              return (c=='x' ? r : (r&0x3|0x8)).toString(16);
          });
          return uuid;
      }
    },
    computed:{
      ...mapState(['code_image_url'])
    },
    mounted(){
        this.Fnimage_code_url()//获取验证码图片
    }
}
</script>
<style lang='stylus' scoped>
.tit
    font-size 32px
    font-weight bold
    text-align center
    line-height 70px
    margin-bottom 60px
.L_inp {
  margin-bottom: 20px;
}
.L_btn {
  width: 100%;
} 
.Verification
    border 0
    width 116px
    border-left 1px solid #cccccc
    line-height 18px
    margin-top 2px
    height 34px
.icLogo
    padding 12px 0px 0px 4px
.ruleFormBox
    margin 0px auto
    width 650px
</style>


